/* App.css */
.login-container {
  display: flex;
  justify-content: center;
  margin-top: 20px; /* Ajusta este valor según lo necesites */
  background-color: gray;
}

.login-form-container {
  padding: 40px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
}

.login-title {
  margin-bottom: 20px;
  text-align: center;
}

.login-input {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
}

.pedir-button {
  width: 180px;
  height: 180px;
  padding: 10px;
  margin: 20px 20px;
  border-radius: 50%;
  border: none;
  background-color: green;
  color: white;
  font-size: 25px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login-button {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 20px 0;
  border-radius: 5px;
  border: none;
  background-color: #5c6bc0;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* Estilos para el contenedor de móviles */
.moviles-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

/* Estilos para cada móvil */
.movil {
  border: 1px solid black;
  border-radius: 5px;
  text-align: left;
  box-sizing: border-box;
  overflow: auto;
  margin: 10px;
  padding: 10px;
  width: calc(50% - 20px); /* Ajusta el ancho para pantallas pequeñas */
}

@media (min-width: 600px) {
  .movil {
    width: 200px; /* Ancho fijo para pantallas más grandes */
  }
}

/* Estilos para el número de móvil */
.movil-number {
  font-size: 1.2em;
  font-weight: bold;
}



.login-button:hover {
  background-color: #3f51b5;
}

.pedir-button:hover {
  background-color: darkgreen;
}

.cancel-button {
  background-color: red;
}

.cancel-button:hover {
  background-color: darkred;
}
